body {
  font-family: "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}

a, a:focus, a:hover {
  color: inherit;
  text-decoration: none;
}

$shop_border: 2px solid #eee;

.swiper-container {
  border-bottom: 4px solid #EEEEEE;
}

.container {
  background-color: #EEEEEE;
  .navbar-fixed-bottom {
    padding-left: 2.5%;
    padding-right: 2.5%;
    text-align: center;
    border-top: 1px solid #EEEEEE;
    background-color: #fff;
    .nav-button {
      padding: 5px 0px 0px 0px;
      display: inline-block;
      width: 18.5%;
      color: #a3a3a3;
      p {
        margin-bottom: 2px;
      }
      img {
        width: 35%;
      }
    }
  }

  .row {
    .col-xs-6:first-child {
      height: 200px;
      img {
        width: 90px;
        float: right;
      }
    }
    .col-xs-6:nth-child(2), .col-xs-6:nth-child(3) {
      height: 100px;
      img {
        position: absolute;
        right: 10px;
        top: 10px;
        width: 42px;
      }
    }
  }
  .col-xs-6 {
    border-right: $shop_border;
    border-bottom: $shop_border;
    height: 162px;
    background-color: #FFFFFF;
    > h4 {
      margin-bottom: 2px;
      font-size: 15px;
      font-weight: bold;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    > p {
      font-size: 12px;
      color: #a3a3a3;
      margin-bottom: 0px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    img {
      width: 80px;
      float: right;
    }
    div {
      position: absolute;
      bottom: 0px;
      left: 10px;
      right: 10px;
      border-top: 1px solid #ddd;
      display: inline-block;
      span {
        font-size: 15px;
        font-weight: bold;
        color: #f71212;
      }
      p {
        float: right;
        margin-top: 5px;
        margin-bottom: 0px;
        font-size: 13px;
        color: #a3a3a3;
      }
    }
  }
  .col-xs-6:last-child {
    margin-bottom: 50px;
  }

}

.nav-active {
  color: #f71212;
}

.category {
  padding: 0px;
  .col-xs-3 {
    padding: 0px;
    text-align: center;
    li {
      padding-top: 8px;
      padding-bottom: 8px;
      font-size: 16px;
      border-right: $shop_border;
      border-bottom: $shop_border;
      background-color: #fff;
    }
    .li-active {
      border-left: 5px solid #f71212;
      padding-right: 5px;
    }
  }

  .col-xs-9 {
    padding: 0px;
  }
}